.catalog {
    margin-top: 70px;
    &__item {
        margin-top: 70px;
        position: relative;
    }
    &__circle {
        background: url('../img/catalog-circle.png') top left no-repeat
    }
    &__content {
        position: relative;
        z-index: 10;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    }
    &__icon {
        width: 26px;
        height: 26px;
    }
    &__title {
        margin: 0;
        padding: 0;
        margin-top: 10px;
        font-weight: 700;
        font-size: 27px;
        text-transform: uppercase;
        color: #171717;
        max-width: 300px;
        sup {
            top: -1em;
            left: 0.3em;
            font-weight: 300;
            font-size: 15px;
            text-align: center;
            color: #FF6600;
        }
    }
    &__nav {
        margin: 0;
        padding: 0;
        margin-top: 50px;
        display: flex;
        flex-direction: column;
        gap: 12px;
    }
    &__link {
        list-style-type: none;
        font-weight: 300;
        font-size: 20px;
        a {
            color: #171717;
            transition: color .2s ease-in-out;
            &:hover {
                color: rgba(23, 23, 23, 0.4);
            }
        }
    }
    &__img {
        max-width: 612px;
        margin-right: 120px;
        margin-top: auto;
    }
    &__num {
        position: absolute;
        bottom: 0;
        left: 20%;
        font-weight: 700;
        font-size: 234px;
        line-height: 60%;
        color: rgba(217, 217, 217, 0.22);
    }
    &__wrap {
        position: absolute;
        bottom: 0;
        right: 60px;
        z-index: 1;
        span {
            color: #FFFFFF;
            position: absolute;
            bottom: 0;
            left: 0;
        }
    }
    &-mobile {
        display: none;
    }
    &__mobile {
        margin-top: 40px;
    }
}



@media (max-width: 1680px) {
	.catalog {
        &__img {
            margin-right: 100px;
            max-width: 452px;
        }
        &__num {
            font-size: 200px;
        }
        &__rectangle {
            width: 500px;
        }
        &__wrap {
            right: 60px;
        }
    }
}

@media (max-width: 1400px) {
	.catalog {
        &__img {
            margin-right: 50px;
            max-width: 412px;
        }
        &__num {
            font-size: 180px;
            left: 18%;
        }
        &__rectangle {
            width: 450px;
        }
        &__title {
            font-size: 24px;
            sup {
                top: -0.8em;
            }
        }
        &__nav {
            margin-top: 40px;
            gap: 10px;
        }
        &__link {
            font-size: 18px;
        }
        &__wrap {
            right: 60px;
            
        }
    }
}

@media (max-width: 1170px) {
	.catalog {
        &__circle {
            background: url('../img/catalog-circle-mobile.png') top left no-repeat
        }
        &__img {
            margin-right: 30px;
            max-width: 324px;
        }
        &__num {
            font-size: 160px;
            left: 18%;
        }
        &__rectangle {
            width: 400px;
        }
        &__title {
            sup {
                top: -0.8em;
                font-size: 12px;
            }
            font-size: 20px;
        }
        &__nav {
            margin-top: 30px;
        }
        &__link {
            font-size: 16px;
        }
        &__wrap {
            right: 60px;
        }
    }
}

@media (max-width: 992px) {
	.catalog {
        display: none;
    }
    .catalog__icon {
        width: 20px;
        height: 20px;
    }
    .catalog__title {
        max-width: 100%;
        padding: 0;
        font-size: 18px;
        margin-top: 5px;
        sup {
            top: -1.2em;
            font-size: 11px;
        }
    }
    .catalog__nav {
        margin-top: 20px;
        gap: 7px;
    }
    .catalog__link a {
        font-size: 14px;
    }
    .catalog-mobile {
        overflow: hidden;
        display: block;
    }
    .catalog__content {
        position: relative;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .catalog__breadcrumbs {
        margin-top: 20px;
    }
    .catalog__block {
        background: #fff;
        padding: 20px;
        width: 100%;
        position: relative;
    }
    .catalog__block:nth-child(2n) {
        background: rgba(217, 217, 217, 0.4);
        .catalog__num {
            color: #fff
        }
    }
    .catalog__num {
        right: 0;
        left: inherit;
        display: inline-block;
        text-align: right;
        font-size: 160px;
        bottom: 20px;
        color: rgba(217, 217, 217, 0.4);
    }
}

@media (max-width: 480px) {
   .catalog__icon {
        width: 14px;
        height: 14px;
    }
    .catalog__title {
        font-size: 14px;
        sup {
            top: -1.2em;
            font-size: 9px;
        }
    }
    .catalog__nav {
        gap: 5px;
    }
    .catalog__link a {
        font-size: 13px;
    }
    .catalog__num {
        right: -20px;
        font-size: 150px;
    }
}